import { createRouter, createWebHistory } from 'vue-router';
import HomePage from '../views/HomePage.vue';
import AboutPage from '../views/AboutPage.vue';
import ViewAll from '../components/ViewAll.vue';
import European from '../components/European.vue';
import Asian from '../components/Asian.vue';
import American from '../components/American.vue';
import African from '../components/African.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomePage,
  },
  {
    path: '/about',
    name: 'About',
    component: AboutPage,
  },
  {
    path: '/view-all',
    name: 'ViewAll',
    component: ViewAll,
  },
  {
    path: '/european',
    name: 'european',
    component: European,
  },
  {
    path: '/asian',
    name: 'asian',
    component: Asian,
  },
  {
    path: '/american',
    name: 'american',
    component: American,
  },
  {
    path: '/african',
    name: 'african',
    component: African,
  },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
